<template>
  <a-modal
    id="addUserModal"
    :title="isEdit ? '编辑用户' : '添加用户'"
    v-model="visible"
    @ok="handleOk"
    width="800px"
    :footer="null"
  >
    <a-form class="kfc" :form="form" @submit="handleSubmit">
      <a-row>
        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          label="用户类型"
          required
        >
          <a-radio-group
            defaultValue="def"
            buttonStyle="solid"
            :value="isVip ? 'vip' : 'def'"
            @change="onUserTypeChange"
          >
            <a-radio-button value="def">普通用户</a-radio-button>
            <a-radio-button value="vip">高级用户</a-radio-button>
          </a-radio-group>
        </a-form-item>
      </a-row>
      <a-row>
        <a-col :span="12">
          <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="昵称"
          >
            <a-input
              maxlength="20"
              v-decorator="[
                'nick',
                { rules: [{ required: true, message: '请输入昵称' }] }
              ]"
              placeholder="请输入昵称(最大长度20)"
            />
          </a-form-item>
          <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="密码"
          >
            <a-input
              minlength="6"
              :type="isEdit ? 'password' : 'text'"
              v-decorator="['password', passwordRules]"
              placeholder="请输入密码(最小长度6)"
            />
          </a-form-item>
          <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="邮箱"
          >
            <a-input
              maxlength="200"
              v-decorator="['email', emailRules]"
              placeholder="请输入邮箱"
            />
          </a-form-item>
        </a-col>
        <a-col :span="12">
          <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="姓名"
          >
            <a-input
              maxlength="10"
              v-decorator="[
                'realName',
                { rules: [{ required: false, message: '请输入姓名' }] }
              ]"
              placeholder="请输入姓名(最大长度10)"
            />
          </a-form-item>
          <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="职位"
          >
            <a-input
              maxlength="20"
              v-decorator="[
                'position',
                { rules: [{ required: false, message: '请输入职位' }] }
              ]"
              placeholder="请输入职位(最大长度20)"
            />
          </a-form-item>
          <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="手机号"
          >
            <a-input
              maxlength="11"
              v-decorator="['mobile', phoneRules]"
              placeholder="请输入手机号(11位)"
            />
          </a-form-item>
        </a-col>
      </a-row>

      <a-row>
        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          label="公司名称"
        >
          <a-input
            maxlength="50"
            v-decorator="[
              'orgName',
              { rules: [{ required: true, message: '请输入公司名称' }] }
            ]"
            placeholder="请输入公司名称(最大长度50)"
          />
        </a-form-item>

        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          label="平台名称"
        >
          <a-input
            maxlength="80"
            :disabled="!isVip"
            v-decorator="[
              'configValue',
              {
                rules: [{ required: true, message: '请输入平台名称' }],
                initialValue: '某某安全控制中心'
              }
            ]"
            placeholder="请输入平台名称(最大长度80)"
          />
        </a-form-item>

        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          label="域名地址"
        >
          <a-input
            :disabled="!isVip"
            maxlength="50"
            v-decorator="[
              'domain',
              {
                rules: [{ required: true, message: '请输入域名地址' }],
                initialValue: 'lingxe.anbai.com'
              }
            ]"
            placeholder="请输入域名地址(最大长度50)"
          />
        </a-form-item>
        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          label="站点数"
        >
          <a-input-number
            :min="10"
            :max="999"
            style="width:100%"
            v-decorator="[
              'siteCount',
              { rules: [{ required: true, message: '请输入站点数' }] }
            ]"
            placeholder="请输入站点数（默认 10，最大999）"
          />
        </a-form-item>
        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          label="所在地"
        >
          <a-input-search
            disabled
            v-model="oldAddress"
            v-if="hideCascader"
            @search="hideCascader = false"
          >
            <a-button slot="enterButton">修改所在地</a-button>
          </a-input-search>

          <a-cascader
            v-if="!hideCascader"
            v-decorator="['cascader', cascaderRules]"
            :options="options"
            changeOnSelect
            :loadData="loadCascaderData"
            @change="onCascaderChange"
            placeholder="请选择所在地"
          />
        </a-form-item>

        <a-row class="ant-form-item" v-if="cascaderResult !== null">
          <a-col :span="4"></a-col>
          <a-col :span="20">
            <a-input
              v-decorator="['details', detailsAddressRules]"
              maxlength="200"
              v-model="details"
              placeholder="请输入详细地址(最大长度200)"
            />
          </a-col>
        </a-row>

        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          label="添加功能权限"
        >
          <a-tree-select
            v-decorator="[
              'permissionIds',
              {
                rules: [{ required: true, message: '请添加功能权限' }],
                initialValue: [17]
              }
            ]"
            style="width: 100%"
            :treeData="authModule"
            treeCheckable
            @change="onChangeAuth"
            :showCheckedStrategy="SHOW_PARENT"
            searchPlaceholder="请添加功能权限"
          />
        </a-form-item>
        <br />
        <a-form-item
          v-if="isVip"
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          label="公司logo："
        >
          <div class="clearfix">
            <a-upload
              v-decorator="[
                'logo',
                { rules: [{ required: false, message: '请添加公司logo' }] }
              ]"
              name="logo"
              listType="picture-card"
              :customRequest="uploadPng"
              :fileList="fileList"
              @preview="handleUploadPreview"
              @change="handleUploadChange"
            >
              <div>
                <a-icon type="plus" />
                <div class="ant-upload-text">Upload</div>
              </div>
            </a-upload>
            <!-- 上传预览 -->
            <a-modal
              :visible="previewVisible"
              :footer="null"
              @cancel="previewVisible = false"
            >
              <img alt="example" style="width: 100%" :src="previewImage" />
            </a-modal>
          </div>
        </a-form-item>

        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          label="账户状态"
        >
          <a-radio-group
            v-decorator="[
              'userStatus',
              { rules: [{ required: true }], initialValue: 1 }
            ]"
          >
            <a-radio :value="1">启用</a-radio>
            <a-radio :value="0">停用</a-radio>
          </a-radio-group>
        </a-form-item>

        <a-form-item
          :label-col="formItemLayout2.labelCol"
          :wrapper-col="formItemLayout2.wrapperCol"
          label="账户类型"
        >
          <a-radio-group
            v-decorator="[
              'accountType',
              { rules: [{ required: true }], initialValue: 0 }
            ]"
            @change="onAccountTypeChange"
          >
            <a-radio :value="0">正式</a-radio>
            <a-radio :value="2">测试</a-radio>
          </a-radio-group>
        </a-form-item>

        <a-row class="ant-form-item">
          <a-col :span="4"></a-col>
          <a-col :span="20">
            <a-date-picker
              :showToday="false"
              format="YYYY-MM-DD"
              placeholder="请选择账号到期时间"
              :value="deadlineEndDate"
              @change=" date => this.deadlineEndDate = date || null ">
              <template slot="renderExtraFooter">
                <a-checkable-tag @change="()=>{this.deadlineEndDate = moment()}" >今天</a-checkable-tag>
                <a-checkable-tag @change="()=>{this.deadlineEndDate = moment().add(3, 'month')}" >三个月</a-checkable-tag>
                <a-checkable-tag @change="()=>{this.deadlineEndDate = moment().add(6, 'month')}" >六个月</a-checkable-tag>
                <a-checkable-tag @change="()=>{this.deadlineEndDate = moment().add(1, 'years')}" >一年</a-checkable-tag>
              </template>
            </a-date-picker>
          </a-col>
        </a-row>

        <br />
        <a-row class="ant-form-item">
          <a-col :span="4"></a-col>
          <a-col :span="20">
            <a-button @click="visible = false">取消</a-button>&nbsp;&nbsp;&nbsp;&nbsp;
            <a-button type="primary" @click="handleSubmit">{{ isEdit ? "更新" : "添加" }}</a-button>
          </a-col>
        </a-row>
      </a-row>
    </a-form>
  </a-modal>
</template>

<script>
import moment from "moment";
import "moment/locale/zh-cn";
moment.locale("zh-cn");

// import cascader from "../plugins/china.json";
import {
  phoneRules,
  emailRules,
  passwordRules,
  cascaderRules,
  detailsAddressRules
} from "../plugins/data.js";

import { addUser, updateUser, getCite, getRSA, getPicture } from "../api/api";

import { TreeSelect } from "ant-design-vue";
const SHOW_PARENT = TreeSelect.SHOW_PARENT;
export default {
  data() {
    return {
      isVip: false,
      phoneRules,
      emailRules,
      passwordRules,
      cascaderRules,
      detailsAddressRules,
      SHOW_PARENT,
      visible: false,
      form: this.$form.createForm(this),
      formItemLayout: {
        labelCol: { span: 8 },
        wrapperCol: { span: 16 }
      },
      formItemLayout2: {
        labelCol: { span: 4 },
        wrapperCol: { span: 20 }
      },
      hideCascader: false,
      isEdit: false,
      uid: null,
      record: null,
      isTestuser: false, // 是不是测试账号,控制测试账号的有效期限日期选择器
      options: [], // 省市县三级联动
      cascaderResult: null, // 省市县三级联动 结果
      oldAddress: null,
      details: null, // 详细地址
      authModule: [], // 功能模块
      autoAuth: [17],
      previewVisible: false, // logo 预览可见性
      previewImage: "", // logo 预览图片地址
      uploadImage: null, // 待上传图片 Base64
      fileList: [], // 上传文件列表
      deadlineEndDate: null, // 有效期
    };
  },
  mounted() {
    this.$nextTick(() => {
      // To disabled submit button at the beginning.
      this.form.validateFields();
    });
  },
  watch: {
    visible: function(value) {
      if (value === false) {
        this.form.resetFields();
        this.isVip = false;
        this.deadlineEndDate = null;
        this.uploadImage = null;
        this.orgAddress = null;
        this.isEdit = false;
        this.hideCascader = false; // 用户地址
        this.oldAddress = null; // 用户地址
        this.isTestuser = false;
        this.fileList = [];
        this.uid = null;
      }
    }
  },
  methods: {
    onChangeAuth(value) {
      // console.log("onChange ", value);
      this.$nextTick(() => {
        if (value[0] !== 18) {
          this.form.setFieldsValue({
            permissionIds: value.concat([17])
          });
        }
      });
    },
    onUserTypeChange(e) {
      this.isVip = e.target.value === "vip";
      this.form.setFieldsValue({
        configValue: this.isVip ? "" : "某某安全控制中心", // 平台名称
        domain: this.isVip ? "" : "lingxe.anbai.com" // 域名地址
      });
    },
    async getLogo(userId){
      try {
        let res = await getPicture({ userId });
        console.log(res);
        if (!res.valid) this.$message.error(res.msg);
        if (res.data && res.data.imageInfo) {
          this.fileList = [
            {
              uid: "-1",
              name: "old",
              status: "done",
              url: res.data.imageInfo
            }
          ];
        }
      } catch (e) {
        window.console.log(e);
      }
    },
    initForm(record, auth) {
      // 数据充填
      this.record = record;
      this.uid = record.user_id;
      this.isEdit = true; // 开启编辑状态
      this.hideCascader = true; // 显示用户地址
      this.oldAddress = record.user_address; // 设置用户地址
      this.isTestuser = record.account_type === 2; // 设置用户类型，显示有效期
      if (record.deadline_end_date) {
        this.deadlineEndDate = moment(record.deadline_end_date); // 设置账户有效期时间
      }
      // 判断高级或者普通用户，高级用户加载 Logo
      if (record.domain && record.domain !== "lingxe.anbai.com"){
        this.getLogo(record.user_id);
        this.isVip = true;
      }

      setTimeout(() => {
        this.form.setFieldsValue({
          nick: record.nick, // 昵称
          password: record.password, // 密码
          mobile: record.mobile, // 手机
          email: record.email, // 邮箱
          realName: record.real_name, // 姓名
          position: record.position, // 职位
          orgName: record.org_name, // 公司名称
          configValue: record.platform_name || "某某安全控制中心", // 平台名称
          domain: record.domain || "lingxe.anbai.com", // 域名地址
          siteCount: record.site_count, // 站点数
          userStatus: record.user_status, // 账户状态
          accountType: record.account_type, // 账户类型
          permissionIds: auth.map(i => i.permission_id) // 权限分配
        });
      });
    },
    moment,
    range(start, end) {
      const result = [];
      for (let i = start; i < end; i++) result.push(i);
      return result;
    },
    disabledDate(current) {
      // Can not select days before today and today
      return current && current < moment().endOf("day");
    },

    disabledDateTime() {
      return {
        disabledHours: () => this.range(0, 24).splice(4, 20),
        disabledMinutes: () => this.range(30, 60),
        disabledSeconds: () => [55, 56]
      };
    },
    async loadCascaderData(selectedOptions) {
      try {
        const targetOption = selectedOptions[selectedOptions.length - 1];
        targetOption.loading = true;
        // console.log(targetOption);

        let res = await getCite({
          postCode: targetOption.value
        });

        if (res.valid) {
          targetOption.loading = false;
          targetOption.children = res.data.map(item => {
            return {
              value: item.post_code,
              label: item.city_name,
              deep: targetOption.deep + 1,
              isLeaf: targetOption.deep + 1 > 1
            };
          });

          this.options = [...this.options];
        }
      } catch (error) {
        window.console.log(error);
      }
    },
    async apiGetCite() {
      try {
        let res = await getCite();
        if (res.valid) {
          this.options = res.data.map(item => {
            return {
              value: item.post_code,
              label: item.city_name,
              deep: 0,
              isLeaf: false
            };
          });
        }
      } catch (error) {
        window.console.log(error);
      }
    },
    //改变账户类型
    onAccountTypeChange() {
      this.$nextTick(() => {
        let bool = Number(this.form.getFieldValue("accountType"));
        this.isTestuser = bool === 2;
      });
    },
    // 上传图片
    uploadPng() {
      // console.log("uploadPng", fileObj.file);
    },
    // 上传处理
    handleUploadChange(data) {
      console.log("handleUploadChange", data);
      // 浏览器检测
      if (typeof FileReader === "undefined") {
        return alert("请使用IE10+或者其他现代浏览器！");
      }
      // 空处理
      if (!data.file.originFileObj) return (this.fileList = []);
      //
      let file = data.file.originFileObj;

      if ( !['image/png','image/jpeg'].includes( file.type) || file.size / 1024 /
      1024 > 1) {
        return this.$message.error("仅支持大小不超过1MB的JPG或者PNG格式图片!");
      }

      const reader = new FileReader();
      reader.addEventListener("load", () => {
        this.uploadImage = reader.result;
        this.fileList = [];
        this.fileList = [
          {
            uid: "-1",
            name: file.name,
            status: "done",
            url: reader.result
          }
        ];
      });
      reader.readAsDataURL(file);
    },
    // 点击预览图片
    handleUploadPreview(file) {
      this.previewImage = file.url || file.thumbUrl;
      this.previewVisible = true;
    },
    // 三级联动
    onCascaderChange(codeList, dataList) {
      if (dataList) {
        this.cascaderResult = dataList.map(i => i.label);
      } else this.cascaderResult = null;
    },
    async api(fn, params) {
      try {
        let res = await fn(params);
        this.$parent.updateUserList();
        this.$message.success(res.msg);
        this.visible = false;
      } catch (error) {
        window.console.log(error);
      }
    },
    //获取数据加密密钥
    async apiGetRSA(values) {
      try {
        let res = await getRSA();
        this.$rsa.setPublicKey(res.data.public_key);
        let params = {
          userId: this.isEdit ? this.uid : undefined,
          orgParentId: 0, // 默认值，父级ID
          parentUserId: "0", // 默认值，父级ID
          nick: values.nick, // 昵称
          mobile: values.mobile, // 手机
          email: values.email, // 邮箱
          realName: values.realName, // 姓名
          position: values.position, // 职位
          orgName: values.orgName, // 公司名称
          configValue: values.configValue, // 平台名称
          domain: values.domain, // 域名地址
          siteCount: values.siteCount, // 站点数
          permissionIds: values.permissionIds.join(","), // 权限分配
          userStatus: values.userStatus, // 账户状态
          accountType: values.accountType, // 账户类型
          password: undefined, // 密码
          deadlineEndDate: this.deadlineEndDate
            ? this.deadlineEndDate.format('YYYY-MM-DD') + " 23:59:59"
            : undefined, // 测试账户保质期时间
          imgBase: this.uploadImage || undefined, // logo
          userAddress: this.cascaderResult
            ? `${this.cascaderResult.join("-")}-${this.details || ""}`
            : undefined
        };
        if (values.domain === "lingxe.anbai.com" && this.isVip)
          return this.$message.error("高级用户域名地址不可设置为 lingxe.anbai.com");

        if (this.isEdit) {
          if (values.password !== this.record.password) {
            let password = this.$rsa.encrypt(values.password);
            if (!password) return this.$message.error("数据加密失败，请重试！");
            params.password = password;
          }
          this.api(updateUser, params); // 编辑
        } else {
          let password = this.$rsa.encrypt(values.password);
          if (!password) return this.$message.error("数据加密失败，请重试！");
          params.password = password;
          this.api(addUser, params); // 添加
        }
      } catch (error) {
        window.console.log(error);
      }
    },
    // 表单提交
    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        // console.log(values);
        if (!err) this.apiGetRSA(values);
      });
    },
    showModal() {
      this.visible = true;
      this.form.resetFields();
    },
    handleOk(e) {
      window.console.log(e);
      this.visible = false;
    }
  }
};
</script>

