import axios from 'axios';
import { Modal } from 'ant-design-vue';

const httpConfig = {
    // baseURL: 'http://10.10.90.30:80',
    // baseURL: 'http://10.10.90.30:8082',
    // baseURL: 'http://192.168.88.47:8082',
    // baseURL: 'http://192.168.88.213:8082',
};

// DEFAULT_MESSAGE(-1, "默认状态码值!"),
// API_EXCEPTION(100, "API接口服务异常!"),
// API_SERVER_EXCEPTION(101, "接口服务访问异常!"),
// AUTH_EXCEPTION(102, "用户授权访问异常!"),
// JWT_TOKEN_EXPIRED_EXCEPTION(103, "用户Token已失效,请重新获取登陆!"),
// JWT_VERIFICATION_EXCEPTION(104, "用户Token验证异常!"),
// JWT_UNKNOWN_TOKEN(105, "未检查到授权信息，授权信息不能为空!"),
// JWT_CLAIM_EXCEPTION(106, "用户Token数据解析异常!"),
// AUTH_ROLE_EXCEPTION(210, "角色不匹配,权限不足!"),
// AUTH_PERMISSION_EXCEPTION(211, "权限校验异常,权限不足!"),
// REQUIRE_AUTH_EXCEPTION(212, "未授权访问,请先获取Token!");

const reloginCode = [102, 103, 104, 105, 106, 212];

const httpCode = {
    400: '错误请求',
    401: '未授权，请重新登录',
    403: '服务器拒绝访问',
    404: '请求错误，未找到该资源',
    405: '请求方法未允许',
    408: '请求超时',
    500: '服务器端出错',
    501: '网络未实现',
    502: '网络错误',
    503: '服务不可用',
    504: '网络超时',
    505: 'http 版本不支持该请求',
};

export default (options = {}) => {
    // 已登录 未登录
    return new Promise((resolve, reject) => {
        // 实例默认值
        let instance = axios.create();
        instance.defaults.baseURL = httpConfig.baseURL;
        instance.defaults.headers['Content-Type'] = 'application/json';

        // 请求拦截器
        instance.interceptors.request.use(
            config => {
                if (typeof options.notoken === 'undefined') {
                    let token = localStorage.getItem('token');
                    if (token) config.headers['Authorization'] = token;
                }
                return config;
            },
            error => Promise.reject(error),
        );

        // 响应拦截器
        instance.interceptors.response.use(
            response => {
                // console.log(options, response);
                // 白名单
                if (response.config.url === '/api/v1/getPicture.do')
                    return response.data;

                if (!response.data.valid) {
                    return Promise.reject(response.data);
                } else return response.data;
            },
            error => Promise.reject(error),
        );

        // 服务端响应处理
        instance(options)
            .then(data => resolve(data))
            .catch(error => {
                let msg = '网络错误（未知）';
                if (error.response) msg = httpCode[error.response.status];
                if (error.msg) msg = error.msg;

                Modal.error({
                    title: '错误提示',
                    content: msg,
                    onCancel: function() {
                        if (
                            error.code &&
                            reloginCode.indexOf(error.code) !== -1
                        ) {
                            window.location.href = '/';
                        }
                    },
                    onOk: function() {
                        if (
                            error.code &&
                            reloginCode.indexOf(error.code) !== -1
                        ) {
                            window.location.href = '/';
                        }
                    },
                });

                reject(error);
            });
    });
};
